import React from "react"
import Layout from "../components/layout"
import Project from "../components/project"
import gameData from "../data/gameData.js"

export default () => (
  <Layout>
  <div id="project-container">
      {gameData.map(project =>
          <Project 
          info={project}
          key={project.id}
          /> )
      }
  </div>
  </Layout>
)