import React, {Component} from "react"
import 'bootstrap/dist/css/bootstrap.css';
import {Button} from 'react-bootstrap';
// import Img from "gatsby-image";
import "../styles/project.scss"


class Project extends Component {
    render() {
        const { name, date, description, built, imageURL, altText} = this.props.info;
        const addText = this.props.info.addText;
        const links = this.props.info.links;

        return (
            <div className="project">
                <div className="project-img-container">
                    <img className="project-img" src={imageURL} alt={altText} />
                </div>
                <div className="project-desc">
                    <h2>{name}</h2>
                    <h3>{date}</h3>
                    <p>{description}</p>
                    <p>{addText}</p>
                    <p>Made with: {built}</p>
                    { links ? (
                    <div className = "project-links">
                    { Object.entries(links).map( ([key, value]) =>
                        <Button a href={value} className="projbtn" variant="outline-info">{key}</Button>  )
                    }
                    </div> ) : ( <div></div>)
                    }
                </div>
            </div>
        );
    }
};

export default Project;